<template>
  <div class="todos">
    <TodoForm @add-item="addItem" :list="list" />
    <TodoList :newLists="list" @id="removeItem" />
    <TodoFooter :list="list" />
  </div>
</template>

<script>
import TodoForm from "./TodoForm.vue";
import TodoList from "./TodoList.vue";
import TodoFooter from "./TodoFooter.vue";
export default {
  data() {
    return {
      list: [
        { id: "001", todoText: "学习", done: true },
        { id: "002", todoText: "吃饭", done: false },
        { id: "003", todoText: "上分", done: false },
      ],
    };
  },
  components: {
    TodoForm,
    TodoList,
    TodoFooter,
  },
  methods: {
    getList(list) {
      this.list = list;
    },
    addItem(item) {
      this.list.push(item);
    },
    changeDone(id) {
      this.list.filter((item) => {
        if (item.id === id) {
          item.done = !item.done;
        }
      });
    },
    removeItem(id) {
      this.list = this.list.filter((el) => {
        return el.id !== id;
      });
      console.log(this.list);
      console.log(this);
    },
    checkAll(value) {
      this.list.forEach((el) => {
        el.done = value;
      });
    },
    clearDone() {
      this.list = this.list.filter((el) => {
        return !el.done;
      });
    },
    changeTodoText(id, text) {
      this.list.map((el) => {
        if (el.id === id) {
          el.todoText = text;
        }
      });
    },
  },
  mounted() {
    this.$bus.$on("changeDone", this.changeDone);
    this.$bus.$on("removeItem", this.removeItem);
    this.$bus.$on("checkAll", this.checkAll);
    this.$bus.$on("clearDone", this.clearDone);
    this.$bus.$on("changeTodoText", this.changeTodoText);
    console.log(this);
  },
};
</script>

<style>
.todos {
  width: 550px;
  margin: 0 auto;
  box-shadow: 0 2px 4px 0 rgb(0 0 0 / 20%), 0 25px 50px 0 rgb(0 0 0 / 10%);
}
</style>
