<template>
  <div class="todo-footer">
    <span
      >{{ total }}
      {{ total === 1 || total === 0 ? "item" : "items" }} left</span
    >
    <div class="btns">
      <button>All</button>
      <button>Active</button>
      <button>Completed</button>
    </div>
    <button class="clear" @click="clearDone" v-show="total">
      Clear completed
    </button>
  </div>
</template>

<script>
export default {
  props: ["list"],
  computed: {
    total() {
      return this.list.filter((el) => !el.done).length;
    },
  },
  methods: {
    clearDone() {
      this.$bus.$emit("clearDone");
    },
  },
};
</script>

<style>
.todo-footer {
  display: flex;
  position: relative;
  padding: 15px;
  justify-content: space-between;
}
.todo-footer span {
  height: 28px;
}
.btns {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.btns button {
  margin: 0 8px;
  background-color: #fff;
  border: 1px solid #efd5d5;
}
.todo-footer button {
  background-color: #fff;
  border: 1px solid #efd5d5;
  padding: 5px;
}
.clear {
  /* display: none; */
}
</style>
