<template>
  <li class="todo-item">
    <input
      type="checkbox"
      name=""
      :id="item.id"
      class="checkbox"
      :checked="item.done"
      @change="changeState(item.id)"
    />
    <label :for="item.id" class="completed">
      <span class="iconfont icon-duihao"></span>
    </label>
    <p
      :class="{ 'p-done': item.done }"
      v-show="!isInput"
      @dblclick="changeText"
    >
      {{ item.todoText }}
    </p>
    <input
      type="text"
      class="edit"
      v-show="isInput"
      v-model="text"
      ref="edit"
      @blur="changeTodoText(item.id)"
    />
    <i class="iconfont icon-cha" @click="remove(item.id)"></i>
  </li>
</template>

<script>
export default {
  props: ["item"],
  data() {
    return {
      isInput: false,
      text: this.item.todoText,
    };
  },
  methods: {
    changeState(id) {
      this.$bus.$emit("changeDone", id);
    },
    remove(id) {
      this.$bus.$emit("removeItem", id);
    },
    // 显示input并获取焦点
    changeText() {
      this.isInput = true;
      this.$nextTick(() => {
        this.$refs.edit.focus();
      });
    },
    changeTodoText(id) {
      this.$bus.$emit("changeTodoText", id, this.text);
      this.isInput = false;
    },
  },
};
</script>

<style>
li {
  display: flex;
  font-size: 24px;
  border-bottom: 1px solid #ededed;
  padding: 20px 15px;
  /* justify-content: space-between; */
  align-items: center;
}
.completed {
  width: 40px;
  line-height: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1px solid #ccc;
  text-align: center;
}
.completed span {
  font-size: 16px;
  display: none;
}
.checkbox {
  display: none;
}
.checkbox:checked + .completed {
  border: 1px solid #5dc2af;
}
.checkbox:checked + .completed span {
  display: block;
  color: #5dc2af;
}
li p {
  flex: 1;
  margin: 0 20px;
}
li .p-done {
  text-decoration: line-through;
  color: #d9d9d9;
}
li i {
  color: #cc9a9a;
}
li i:hover {
  color: #af5b5e;
}
.edit {
  flex: 1;
  height: 30px;
  margin: 0 20px;
  font-size: 24px;
}
</style>
