<template>
  <div class="todo-form">
    <i
      class="iconfont icon-xiangxia2"
      :class="{ 'i-active': isAll }"
      @click="isAll = !isAll"
    ></i>
    <input type="text" v-model="text" @keyup.enter="sendItem" />
    <!-- <button>添加</button> -->
  </div>
</template>

<script>
import { nanoid } from "nanoid";
export default {
  data() {
    return {
      text: "",
    };
  },
  props: ["list"],
  computed: {
    total() {
      return this.list.filter((el) => el.done).length;
    },
    isAll: {
      get() {
        return this.total === this.list.length;
      },
      set(value) {
        this.$bus.$emit("checkAll", value);
      },
    },
  },
  methods: {
    sendItem() {
      if (this.text.trim()) {
        this.$emit("add-item", {
          id: nanoid(),
          todoText: this.text,
          done: false,
        });
        this.text = "";
      }
    },
  },
};
</script>

<style>
.todo-form {
  display: flex;
  position: relative;
}
.todo-form input {
  width: 100%;
  outline: none;
  border: none;
  line-height: 65px;
  background: rgba(0, 0, 0, 0.003);
  box-shadow: inset 0 -2px 1px rgb(0 0 0 / 3%);
  text-indent: 50px;
  font-size: 24px;
}
.todo-form i {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 20px;
  color: #ebe8e6;
  left: 20px;
  cursor: pointer;
}
.todo-form .i-active {
  color: #737373;
}
</style>
